import React from "react";
import PropTypes from "prop-types";
import styled from "astroturf";

import { Button } from "./Buttons";

export const IconButtonContainer = styled(Button, null, "IconButtonContainer", require("./IconButton-IconButtonContainer.module.scss"), "icon-button-container", "iconButtonContainer");

export const IconButton = ({ children, ...props }) => {
  return <IconButtonContainer {...props}>{children}</IconButtonContainer>;
};

IconButton.propTypes = {
  component: PropTypes.bool,
};
